import React, {Component} from 'react';
import CRUDTable, {CreateForm, DeleteForm, Field, Fields, UpdateForm} from "react-crud-table";
import TableService from "../services/TableService";

const Table = () => {
    const DescriptionRenderer = ({field}) => <textarea {...field} />;

    const SORTERS = {
        NUMBER_ASCENDING: mapper => (a, b) => mapper(a) - mapper(b),
        NUMBER_DESCENDING: mapper => (a, b) => mapper(b) - mapper(a),
        STRING_ASCENDING: mapper => (a, b) => mapper(a).localeCompare(mapper(b)),
        STRING_DESCENDING: mapper => (a, b) => mapper(b).localeCompare(mapper(a))
    };
    const styles = {
        container: {margin: "auto", width: "fit-content"}
    };
    const getSorter = data => {
        const mapper = x => x[data.field];
        let sorter = SORTERS.STRING_ASCENDING(mapper);

        if (data.field === "id") {
            sorter =
                data.direction === "ascending"
                    ? SORTERS.NUMBER_ASCENDING(mapper)
                    : SORTERS.NUMBER_DESCENDING(mapper);
        } else {
            sorter =
                data.direction === "ascending"
                    ? SORTERS.STRING_ASCENDING(mapper)
                    : SORTERS.STRING_DESCENDING(mapper);
        }

        return sorter;
    };
    return (
        <div style={styles.container}>
            <CRUDTable
                caption="Student List"
                fetchItems={()=>TableService.getTable("authors")}
            >
                <Fields>
                    <Field name="id" label="Id" hideInCreateForm hideInUpdateForm/>
                    <Field name="name" label="Name" placeholder="Name"/>
                    <Field
                        name="description"
                        label="Description"
                        render={DescriptionRenderer}
                    />
                </Fields>
                <CreateForm
                    name="Student Creation"
                    message="Create a new student!"
                    trigger="Create Student"
                    // onSubmit={student => service.create(student)}
                    submitText="Create"
                    validate={values => {
                        const errors = {};
                        if (!values.name) {
                            errors.name = "Please, provide student's name";
                        }

                        if (!values.description) {
                            errors.description = "Please, provide student's description";
                        }

                        return errors;
                    }}
                />

                <UpdateForm
                    name="Student Update Process"
                    message="Update student"
                    trigger="Update"
                    // onSubmit={student => service.update(student)}
                    submitText="Update"
                    validate={values => {
                        const errors = {};
                        if (!values.name) {
                            errors.name = "Please, provide student's name";
                        }

                        if (!values.description) {
                            errors.description = "Please, provide stundent's description";
                        }

                        return errors;
                    }}
                />

                <DeleteForm
                    name="Student Delete Process"
                    message="Are you sure you want to delete student?"
                    trigger="Delete"
                    // onSubmit={student => service.delete(student)}
                    submitText="Delete"
                    validate={values => {
                        const errors = {};
                        if (!values.id) {
                            errors.id = "Please, provide id";
                        }
                        return errors;
                    }}
                />
            </CRUDTable>
        </div>

    );

}

export default Table;