import axios from 'axios';

const BASE_URL = 'http://localhost:8080'; // Update with your backend URL

const TableService = {
    getTable: (tableName) => {
        return axios.get(`${BASE_URL}/${tableName}`);
    },

    addRecord: (tableName, recordData) => {
        return axios.post(`${BASE_URL}/${tableName}/records`, recordData);
    },

    updateRecord: (tableName, recordId, newData) => {
        return axios.put(`${BASE_URL}/${tableName}/records/${recordId}`, newData);
    },

    deleteRecord: (tableName, recordId) => {
        return axios.delete(`${BASE_URL}/${tableName}/records/${recordId}`);
    },

};

export default TableService;